<template>
	<div id="jspdfjs">
		<label for="first">Nmero da primeira pgina</label><br>
		<input type="number" v-model="firstPage" id="firstp"><br>
		<label for="totalp">Total de pginas alm da primeira</label><br>
		<input type="number" v-model="pages" id="totalp"><br>
		<input type="radio" name="ehfrente" checked @click="setFrente"> Frente <br>
		<input type="radio" name="ehfrente" @click="setVerso"> Verso <br>
		<button @click="create(ehfrente)">Gerar carimbadas</button>	
	</div>
</template>

<script>
	import JsPDF from '../../static/jspdf.min.js'

	export default {
		name: 'jspdfjs',
		data() {
			return {
				firstPage: 0,
				pages: 0,
				ehfrente: true
			}
		},
		methods: {
			setFrente: function() {
				this.ehfrente = true
			},
			setVerso: function() {
				this.ehfrente = false
			},
			create: function(isFrente) {
				const doc = new JsPDF()
				let total = parseInt(this.firstPage) + parseInt(this.pages)
				for (let x = 0; x <= this.pages; x++) {
					if (isFrente) {
						doc.addImage(imgData, 'JPEG', 180, 2, 28, 28)
						doc.text(total.toString(), 190, 13)
					} else {
						doc.addImage(imgData, 'JPEG', 2, 2, 28, 28)
						doc.text(total.toString() + 'V', 11, 13)
					}
					if (total > this.firstPage) {
						doc.addPage()
					}
					total--
				}
				doc.save('a4.pdf')
			}
		}
	}

	const imgData = 'data:image/jpeg;base64,'
</script>

<style>
	@import url('../../static/bootstrap/css/bootstrap.min.css');

</style>



